import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'

import Login from './../pages/Login'
import EntryList from './../pages/EntryList'
import JudgingPanel from './../pages/JudgingPanel'
import ReviewScoring from './../pages/ReviewScoring'
import FinalizedSummary from './../pages/FinalizedSummary'

import { tagEntryAsDone, populateFinalScores, addCriteriaScore, updateCriteriaScore, addEntryComments, updateEntryComments, populateCriteriaList, populateUserDetails, populateEntriesList, populateEntryCriteriaList, populateEntryCriteriaScoreList } from './../config/actions'

export const LoginContainer = withRouter(connect(
    ({ userDetails, events }, { history }) => (
        { 
            userDetails : userDetails,
            events : events,
            history : history 
        }
    ),
    dispatch => ({
        populateUserDetails(userDetails) {
            dispatch(populateUserDetails(userDetails))
        },
        populateCriteriaList(criteraList) {
            dispatch(populateCriteriaList(criteraList))  
        },
        populateEntriesList(entryList) {
            dispatch(populateEntriesList(entryList))
        },
        populateEntryCriteriaList(entryCriteriaList) {
            dispatch(populateEntryCriteriaList(entryCriteriaList))
        },
        populateEntryCriteriaScoreList(entryCriteriaScoreList) {
            dispatch(populateEntryCriteriaScoreList(entryCriteriaScoreList))
        }
    })
)(Login))

export const EntryListContainer = withRouter(connect(
    ({ userDetails, entries, entryCriteriaScores }, { history }) => (
        { 
            entries : entries, 
            userDetails : userDetails, 
            entryCriteriaScores : entryCriteriaScores, 
            history : history
        }
    ),
//    dispatch => ({})
    null
)(EntryList))

export const ReviewScoringContainer = withRouter(connect(
    ({ entries, entryCriteriaScores, criterias }, { history }) => (
        { 
            entries : entries, 
            criterias : criterias, 
            entryCriteriaScores : entryCriteriaScores, 
            history : history
        }
    ),
//    dispatch => ({})
    null
)(ReviewScoring))

export const FinalizedSummaryContainer = withRouter(connect(
    ({ entries, entryCriteriaScores, finalScores, criterias }, { history }) => (
        { 
            entries : entries, 
            criterias : criterias, 
            finalScores : finalScores, 
            entryCriteriaScores : entryCriteriaScores, 
            history : history
        }
    ),
    dispatch => ({
        populateFinalScores(finalScores) {
            dispatch(populateFinalScores(finalScores))
        }
    })
)(FinalizedSummary))

export const JudgingPanelContainer = withRouter(connect(
    ({ criterias, entryComments, entries, entryCriteriaScores }, { history, match }) => (
        { 
            history : history, 
            entries : entries, 
            entryCriteriaScores : entryCriteriaScores, 
            entryComments : entryComments, 
            criterias : criterias,
            entryId : match.params.entryId
        }
    ),
    dispatch => ({
        addCriteriaScore(criteriaScoreDetails) {
            dispatch(addCriteriaScore(criteriaScoreDetails))
        },
        updateCriteriaScore(criteriaScoreDetails) {
            dispatch(updateCriteriaScore(criteriaScoreDetails))
        },
        addEntryComments(entryComment) {
            dispatch(addEntryComments(entryComment))
        },
        updateEntryComments(entryComment) {
            dispatch(updateEntryComments(entryComment))
        },
        tagEntryAsDone(tagObject) {
            dispatch(tagEntryAsDone(tagObject))
        }
        
    })
)(JudgingPanel))


