import actionConstants from './actionConstants'

export const populateUserDetails = (userDetails) => (
    {
        type : actionConstants.POPULATE_USER_DETAILS,
        id : userDetails.id,
        username : userDetails.userName,
        userType : userDetails.type
    }
)

export const populateEntriesList = (entryList) => (
    {
        type : actionConstants.POPULATE_ENTRY_LIST,
        entriesList : entryList
    }
)

export const populateEntryCriteriaList = (entryCriteriaList) => (
    {
        type : actionConstants.POPULATE_ENTRY_CRITERIA_LIST,
        entryCriteriaList : entryCriteriaList
    }
)

export const populateEntryCriteriaScoreList = (entryCriteriaScoreList) => (
    {
        type : actionConstants.POPULATE_ENTRY_CRITERIA_SCORE_LIST,
        entryCriteriaScoreList : entryCriteriaScoreList
    }
)

export const addCriteriaScore = (criteriaScoreDetails) => (
    {
        type : actionConstants.CRITERIA_ADD_SCORE,
        entryId : criteriaScoreDetails.entryId,
        criteriaId : criteriaScoreDetails.criteriaId,
        criteriaName : criteriaScoreDetails.criteriaName,
        score : criteriaScoreDetails.score
    }
)

export const updateCriteriaScore = (criteriaScoreDetails) => (
    {
        type : actionConstants.CRITERIA_UPDATE_SCORE,
        scoreId : criteriaScoreDetails.scoreId,
        entryId : criteriaScoreDetails.entryId,
        criteriaId : criteriaScoreDetails.criteriaId,
        judgeId : criteriaScoreDetails.judgeId,
        score : criteriaScoreDetails.score
    }
)

export const populateCriteriaList = (criterias) => (
    {
        type: actionConstants.POPULATE_CRITERIA_LIST,
        criteriaList : criterias
    }
)

export const addEntryComments = (commentEntry) => (
    {
        type: actionConstants.ENTRY_ADD_COMMENTS,
        entryId : commentEntry.entryId,
        note : commentEntry.comments
    }
)

export const updateEntryComments = (commentEntry) => (
    {
        type: actionConstants.ENTRY_UPDATE_COMMENTS,
        entryId : commentEntry.entryId,
        note : commentEntry.comments
    }
)

export const tagEntryAsDone = (tagObject) => (
    {
        type: actionConstants.ENTRY_TAG_AS_DONE,
        entryId : tagObject.entryId,
        done : tagObject.done
    }
)

export const populateFinalScores = (finalScores) => (
    {
        type: actionConstants.POPULATE_FINALIZED_SCORES,
        finalScoresList : finalScores
    }
)

