import actionConstants from "./actionConstants"

export const userDetails = (state={}, action) => {
    switch ( action.type ) {
        case actionConstants.POPULATE_USER_DETAILS : 
            return {
                id : action.id,
                username : action.username,
                userType : action.userType
            }
            
        default : 
            return state
    }
}

export const events = (state=[], action) => {
    switch ( action.type ) {
        case actionConstants.POPULATE_EVENTS_LIST : 
            return state = action.eventsList
        
        default : 
            return state
    }
}

export const event = (state={}, action) => {
    switch ( action.type ) {
        case actionConstants.EVENT_ADD_DETAILS : 
            return {
                id : action.id,
                name : action.name,
                description : action.description
            }
            
        default : 
            return state
    }
}

export const entries = (state=[], action) => {
    switch ( action.type ) {
        case actionConstants.POPULATE_ENTRY_LIST : 
            return state = action.entriesList
            
        case actionConstants.ENTRY_TAG_AS_DONE : 
        case actionConstants.ENTRY_UPDATE_COMMENTS : 
            return state.map(entryItem => entry(entryItem, action))
            
        default : 
            return state
    }
}

