export const entry = (state={}, action) => {
    switch ( action.type ) {
        case actionConstants.ENTRY_ADD_DETAILS : 
            return {
                id : action.id,
                name : action.name,
                description : action.desc
            }
            
        case actionConstants.ENTRY_UPDATE_COMMENTS : 
            return (state.entryId === action.entryId) ? {
                ...state,
                note : action.note
            }
            :
            state
            
        case actionConstants.ENTRY_TAG_AS_DONE : 
            return (state.entryId === parseInt(action.entryId)) ? {
                ...state,
                done : action.done
            }
            :
            state 
            
        default : 
            return state
    }
}

