export const entryComments = (state=[], action) => {
    switch ( action.type ) {
        case actionConstants.ENTRY_ADD_COMMENTS : 
            return [...state, entryComment({}, action)]
        
        
        case actionConstants.ENTRY_UPDATE_COMMENTS : 
            return state.map(entry => entryComment(entry, action))
            
        default :
            return state
    }
}

export const entryComment = (state={}, action) => {
    switch ( action.type ) {
        case actionConstants.ENTRY_ADD_COMMENTS : 
            return {
                entryId : action.entryId,
                comments : action.comments
            }
            
        case actionConstants.ENTRY_UPDATE_COMMENTS : 
            return (state.entryId === action.entryId) ? {
                ...state,
                comments : action.comments
            }
            :
            state
             
  
        
        default : 
            return state
    }
}

export const finalScores = (state=[], action) => {
    switch ( action.type ) {
        case actionConstants.POPULATE_FINALIZED_SCORES : 
            return state = action.finalScoresList
            
        default : 
            return state
    }
}

