import React, { Component } from 'react'
import './../css/confirmation-modal.css'

function confirmModalAction (closeModalFunction, actionFunction, historyObj) {
    actionFunction(); // TODO: remove when action function is already defined
    closeModalFunction();
    
}

const ConfirmationModal = ({closeModalFunc, modalDetailsObject, historyObj}) => {
    return (
        <div className="modal-bd">
            <div className="modal-content">
                <div className="modal-header">
                    <span><strong>{modalDetailsObject.headerText}</strong></span>
                    <span id="close-modal-button" onClick={closeModalFunc}>X</span>
                </div>
                <div className="modal-body">
                    <span>{modalDetailsObject.bodyText}</span>
                </div>
                { 
                    ("CONFIRM" === modalDetailsObject.modalType) ? 
                    <div className="modal-footer">
                        <button className="btn btn-danger" onClick={closeModalFunc}>CANCEL</button>
                        <button className="btn btn-success" onClick={() => confirmModalAction(closeModalFunc, modalDetailsObject.actionFunc, historyObj)}>OK</button>
                    </div>
                    :
                    <div className="modal-footer">
                        <button className="btn btn-danger" onClick={closeModalFunc}>NO</button>
                        <button className="btn btn-success" onClick={() => this.confirmModalAction(closeModalFunc, modalDetailsObject.actionFunc, historyObj)}>YES</button>
                    </div>
                }
            </div>
        </div>
    )
}

export default ConfirmationModal;


