import React, { Component } from 'react'

class EntryComments extends Component {
    constructor() {
        super ()
        
        this.state = {
            entryComment : ""
        }
        
        this.onCommentEntry = this.onCommentEntry.bind(this)
    }
    
    componentWillMount() {
        const { entryCommentsList } = this.props;
        const { entryCommentAddFunc } = this.props;
        
        var filteredList = entryCommentsList.filter(entryComment => entryComment.entryId === this.props.entryId)
        var isEntryCommentExisting = (filteredList.length > 0) ? true : false
        
        if (!isEntryCommentExisting) {
            entryCommentAddFunc(
                {
                    entryId : this.props.entryId,
                    comments : ""
                }
            )
        }
    }
    
    onCommentEntry (comment) {
        const { entryCommentUpdateFunc } = this.props;
        
        this.setState({entryComment : comment.value})
        
        entryCommentUpdateFunc(
            {
                entryId : this.props.entryId,
                comments : comment.value
            }
        )
    }
    
    render () {
        var entryCommentHolder;
        
        var filteredList = this.props.entryList.filter(entry => entry.entryId === this.props.entryId)
        var defaultComment = filteredList[0] ? filteredList[0].note : ""
        
        return (
            <div>
                <textarea 
                    id="entry-judging-comments" 
                    placeholder="Enter comments here..." 
                    defaultValue={defaultComment}
                    ref={ref => entryCommentHolder = ref}
                    onBlur={() => this.onCommentEntry(entryCommentHolder)}
                />
            </div>
        )
    }
}

export default EntryComments


