import React, { Component } from 'react'
import { Row, Col } from 'react-bootstrap'

class CriteriaScoring extends Component {
    constructor () {
        super () 
        
        this.state = {
            sliderScore : 0
        }
        
        this.sliderValueChange = this.sliderValueChange.bind(this)
    }
    
    componentWillMount () {
        const { criteria, criteriaScoresList, entryId } = this.props;
        const { scoreAddFunc, scoreAvgFunc } = this.props;
        
        var filteredCriteriaScoreList = criteriaScoresList.filter(criteriaScore => criteriaScore.criteriaId === criteria.criteriaId && criteriaScore.entryId === parseInt(entryId))
        var isScoreExisting = (filteredCriteriaScoreList.length > 0) ? true : false;
//        
//        var scoringObject = {
//            entryId : 1,
//            criteriaId : criteria.id,
//            criteriaName : criteria.name,
//            score : 1
//        }
//        
//        if (!isScoreExisting) {
//            scoreAddFunc(scoringObject); 
//        }
        
        if (filteredCriteriaScoreList[0]) {
            this.setState({sliderScore : filteredCriteriaScoreList[0].score})
        }
        
        scoreAvgFunc();
    }
    
    sliderValueChange (newValue, criteriaId) {
        
        const { criteria, entryId } = this.props;
        const { scoreUpdateFunc, scoreAvgFunc } = this.props;
        
        var scoringObject = {
            entryId : parseInt(entryId),
            score : parseInt(newValue),
            ...criteria
        }
        scoreUpdateFunc(scoringObject) 
        this.setState({sliderScore : newValue}, () => scoreAvgFunc())
    }
    
    render () {
        var sliderValue = 1;
        const { criteria, criteriaScoresList, entryId } = this.props;
        
        var filteredCriteriaScoreList = criteriaScoresList.filter(criteriaScore => criteriaScore.criteriaId === criteria.criteriaId && criteriaScore.entryId === parseInt(entryId))
        var defaultValue = filteredCriteriaScoreList[0] ? filteredCriteriaScoreList[0].score : 0;
        
        return (
            <Row className="judging-criteria-container">
                <Col md={5}>
                    <span>{criteria.criteriaName}</span>
                </Col>
                <Col md={6}>
                    <input 
                        className="criteria-score-slider"
                        type="range"
                        min={criteria.minValue}
                        max={criteria.maxValue}
                        defaultValue={defaultValue}
                        ref={ ref => sliderValue = ref }
                        onInput={() => this.sliderValueChange(sliderValue.value)}
                    />
                </Col>
                <Col md={1}>
                    <span>{this.state.sliderScore}</span>
                </Col>
            </Row>
        )
    }
}

export default CriteriaScoring;


