import React, { Component } from 'react';

import { Provider } from 'react-redux'
import { BrowserRouter, HashRouter, Route, Redirect } from 'react-router-dom'

import storeFactory from './config/storeFactory'
import MainBody from './common/MainBody'
import { LoginContainer } from './pages/containers'

/** CSS **/
import './../node_modules/bootstrap/dist/css/bootstrap.min.css'
import './css/App.css';
import './css/login.css';
import './css/entry-list.css';
import './css/judging-panel.css';
import './css/criteria-scoring.css';
import './css/review-scoring.css';

const store = storeFactory();
store.subscribe(() => {
    console.warn("New store state:", store.getState());
    localStorage['judge-app-store'] = JSON.stringify(store.getState());
})

class App extends Component {
  render() {
    return (
        <Provider store={store} >
            <HashRouter>
                <div className="App">
                    <Route exact path="/" component={() => <Redirect to="/login" />} />
                    <Route exact path="/login" component={ LoginContainer } />
                    <Route path="/judge-app" component={ MainBody } />
                </div>
            </HashRouter>
        </Provider>
    );
  }
}

export default App;



