import React, { Component } from 'react'
import { Grid, Row, Col } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'
import ConfirmationModal from './../components/ConfirmationModal'
import { putCall, postCall } from './../connection/connectionUtil'

class EntryList extends Component {
    constructor() {
        super()
        
        this.state = {
            displaySubmitModal : false
        }
        
        this.toggleDisplayConfirmationModal = this.toggleDisplayConfirmationModal.bind(this);
        this.submitFinalScores = this.submitFinalScores.bind(this);
    }
    
    toggleDisplayConfirmationModal() {
        this.setState({displaySubmitModal : !this.state.displaySubmitModal})
    }
    
    submitFinalScores() {
        const eventId = 2;
        this.props.entryCriteriaScores.map(entryScores => {
            var scoreObject = {
                criteriaId: entryScores.criteriaId,
                done: true,
                entryId: entryScores.entryId,
                eventId: eventId,
                final: true,
                judgeId: entryScores.judgeId,
                score: entryScores.score,
                scoreId: entryScores.scoreId
            }
            
            putCall("scores", scoreObject).then(updateScoreResp => {console.warn(updateScoreResp)})
        })
        
        var finalizeScoreUrl = "scores/finalize/" + eventId + "/" + this.props.userDetails.id
        postCall(finalizeScoreUrl,{}).then(finalizeResp => {console.warn("RESPONSE", finalizeResp)});
        
        this.props.history.push("/judge-app/overall-judging-summary")
    }
    
    render () {
        const { entries, history } = this.props
        
        const filteredEntries = entries.filter(entry => false === entry.done)
        const isAllDone = filteredEntries.length > 0 ? false : true
        
        var confirmationModalObject = {
            modalType : "CONFIRM",
            headerText : "Submission Confirmation",
            bodyText : "By submitting, all the scores you provided for all the entries under you will be recorded on the database. Further changes after confirming that these are the final scores, will be subject for approval.",
            actionFunc : this.submitFinalScores, // TODO : what to submit?
            actionFuncParam : {} // TODO : what to submit?
        }
        
        return (
            <Grid className="header-padding max-height">
                <div id="entry-list-container">
                    <div id="entry-list-container-header">
                        List of Entries
                    </div>
                    <div id="entry-container-divider"></div>
                    <div id="entry-list-section">
                        {
                            entries.map(entry => 
                                <Row key={entry.entryId} className="entry-display-item">
                                    <Col md={8} className="section-entry-name">
                                        <span>{entry.entryName}</span>
                                    </Col>
                                    <Col md={2} className={"entry-status-holder" + (entry.done ? " entry-done" : " entry-not-done")}>
                                        <div className="entry-status"><span className="entry-stat-text">{entry.done ? "Done" : "Not Done"}</span></div>
                                    </Col>
                                    <Col md={2} className="entry-test-opt">
                                        <NavLink className="btn btn-primary" to={`/judge-app/judging-panel/${entry.entryId}`}>Judge</NavLink>
                                    </Col>
                                    
                                </Row>
                            )
                        }
                    </div>
                    <div id="entry-container-divider"></div>
                    <div id="entry-list-options-section">
                        <NavLink to="/judge-app/review-scoring" className="btn btn-primary">Review</NavLink>
                        {
                            (isAllDone) ?
                            <button className="btn btn-success" onClick={this.toggleDisplayConfirmationModal} >Submit</button>
                            :
                            <button className="btn btn-success disabled">Submit</button>    
                        }                        
                    </div>
                </div>
                {
                    (true === this.state.displaySubmitModal) ?
                        <ConfirmationModal 
                            closeModalFunc={this.toggleDisplayConfirmationModal} 
                            modalDetailsObject={confirmationModalObject} 
                            historyObj={history}
                        />
                    :
                    ""
                }
            </Grid>
        )
    }
}

export default EntryList;


