import axios from 'axios'

const CONNECTION_URL = "http://18.236.173.69/dredd/api/"
const TOKEN_CONNECTION = "http://18.236.173.69/"

export const postCall = (methodCall, requestParam) => {
    var webMethodCall = formWebMethodCall(methodCall);
    return axios.post(webMethodCall, requestParam);
}

export const getCall = (methodCall) => {
    var webMethodCall = formWebMethodCall(methodCall);
    return axios.get(webMethodCall)
}

export const putCall = (methodCall, requestParam) => {
    var webMethodCall = formWebMethodCall(methodCall);
    return axios.put(webMethodCall, requestParam);
}

export const deleteCall = (methodCall, requestParam) => {
    var webMethodCall = formWebMethodCall(methodCall);
    return axios.delete(webMethodCall);
}

export const uploadPostCall = (methodCall, requestParam) => {
    let formData = new FormData();
    const config = { 
        headers: {
            'Content-Type': 'multipart/form-data', 
            'Authorization' : localStorage['user-token']
        }
    }
    
    var webMethodCall = formWebMethodCall(methodCall);
    
    formData.append('file', requestParam);

    return axios.post(webMethodCall, formData, config);
}

const formWebMethodCall = (methodCall) => CONNECTION_URL + methodCall


