import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { Grid } from 'react-bootstrap'
import { getCall } from './../connection/connectionUtil'

var username, password;

class Login extends Component {
    constructor(props) {
        super(props)
        
        this.state = {
            username: null,
            password: null
        }
        
        this.onLogin = this.onLogin.bind(this);
        this.processEntriesList = this.processEntriesList.bind(this);
        this._handleKeyPress = this._handleKeyPress.bind(this);
    }
    
    processEntriesList(eventDetails) {
        var processedEntriesList = eventDetails.entries.map(entry => {
            var {criteria, ...other} = entry
            return other
        })
        
        var processedEntryCriterias = []
        var processedEntryCriteriaScores = []
        
        eventDetails.entries.map(entry => {
            entry.criteria.map(crta => {
                var {score, ...other} = crta
                
                other.entryId = entry.entryId
                processedEntryCriterias.push(other)
                
                score.entryId = entry.entryId
                score.criteriaId = other.criteriaId
                processedEntryCriteriaScores.push(score)
            })
        })
        
        return {
            entryList : processedEntriesList,
            criteriaList : processedEntryCriterias,
            criteriaScoresList : processedEntryCriteriaScores
        }
    }
    
    onLogin() {
        var loginUrl = "login/" + username.value + "," + password.value;
        
        const { populateUserDetails, populateEntriesList, populateCriteriaList, populateEntryCriteriaList, populateEntryCriteriaScoreList, history } = this.props;
        
        getCall(loginUrl).then(loginResponse => {
            populateUserDetails(loginResponse.data)
            var eventId = 2
            var getEventDetailsUrl = "mainevent/" + eventId + "/" + loginResponse.data.id;
            getCall(getEventDetailsUrl).then(eventDetailsResp => {
                var processedLists = this.processEntriesList(eventDetailsResp.data)
                
                populateEntriesList(processedLists.entryList);
                populateEntryCriteriaList(processedLists.criteriaList);
                populateEntryCriteriaScoreList(processedLists.criteriaScoresList);
            })  
            
            var getCriteriaListUrl = "criteriabyevent/" + eventId;
            getCall(getCriteriaListUrl).then(criteriaListResp => {
                populateCriteriaList(criteriaListResp.data);
            })
            
            history.push("/judge-app/entry-list")
        })
    }
    
    _handleKeyPress(e) {
        if ("Enter" === e.key) {
            this.onLogin();
        }
    }
    
    render () {
        return(
            <Grid className="header-padding">
                <div id="login-container">
                    <div id="login-title-section">
                        <span>JUDGE DREDD JUDGING APP</span>
                    </div>
                    <div id="login-form-section">
                        <div>
                            <input 
                                className="form-control" 
                                type="text" 
                                placeholder="username" 
                                onKeyPress={this._handleKeyPress} 
                                ref={ref => username = ref}
                            />
                        </div>
                        <div>
                            <input 
                                className="form-control" 
                                type="password" 
                                placeholder="password" 
                                onKeyPress={this._handleKeyPress} 
                                ref={ref => password = ref}
                            />
                        </div>
                    </div>
                    <div id="login-opt">
                        <button className="btn btn-success" onClick={ this.onLogin }>Login</button>
                    </div>
                </div>
            </Grid>
        )
    }
}

export default Login;


