import { createStore, combineReducers } from 'redux'
import { events, entries, finalScores, criterias, criteriaScores, entryComments, userDetails, entryCriterias, entryCriteriaScores } from './reducers'

const initialData = {
    events : [],
    entries : [],
    criterias : [],
    criteriaScores : [],
    entryComments : [],
    entryCriterias : [],
    entryCriteriaScores : [],
    finalScores : [], 
    userDetails : {}
}

const storeFactory = () => 
    createStore(
        combineReducers({ events, entries, finalScores, criterias, criteriaScores, entryComments, userDetails, entryCriterias, entryCriteriaScores }),
        (localStorage["judge-app-store"]) ? JSON.parse(localStorage["judge-app-store"]) : initialData
    )

export default storeFactory


