import React, { Component } from 'react'
import { Grid, Row, Col } from 'react-bootstrap'
import CriteriaScoring from './../components/CriteriaScoring'
import EntryComments from './../components/EntryComments'
import ConfirmationModal from './../components/ConfirmationModal'

var testCriterias = [
    {
        id : 1,
        name : "Criteria 1",
        desc : "This is for Criteria 1"
    },
    {
        id : 2,
        name : "Criteria 2",
        desc : "This is for Criteria 1"
    },
    {
        id : 3,
        name : "Criteria 3",
        desc : "This is for Criteria 1"
    },
    {
        id : 4,
        name : "Criteria 4",
        desc : "This is for Criteria 1"
    }
]

class JudgingPanel extends Component {
    constructor() {
        super() 
        
        this.state = {
            avgScore : 1,
            displaySubmitModal : false
        }
        
        this.calculateAverageScore = this.calculateAverageScore.bind(this);
        this.toggleDisplayConfirmationModal = this.toggleDisplayConfirmationModal.bind(this);
        this.tagEntryDoneAndRedirect = this.tagEntryDoneAndRedirect.bind(this);
    }
    
    calculateAverageScore () {
        var totalScore = 0;
        
        var { entryCriteriaScores, criterias, entryId } = this.props;
        
        entryCriteriaScores.map(criteriaScore => {
            if (criteriaScore.entryId === parseInt(entryId)) {
                totalScore = totalScore + criteriaScore.score
            }
        })
        
        var average = totalScore / criterias.length;
        this.setState({avgScore : average})
    }
    
    toggleDisplayConfirmationModal() {
        this.setState({displaySubmitModal : !this.state.displaySubmitModal})
    }
    
    tagEntryDoneAndRedirect() {
        const {tagEntryAsDone, entryId, history} = this.props;
        
        tagEntryAsDone({entryId : parseInt(entryId), done : true});
        history.push("/judge-app/entry-list");
    }
    
    render () {
        var sliderValue
        
        const { history, entryCriteriaScores, entryComments, criterias, entries, entryId } = this.props;
        const { tagEntryAsDone, addCriteriaScore, updateCriteriaScore, addEntryComments, updateEntryComments } = this.props;
        
        var confirmationModalObject = {
            modalType : "CONFIRM",
            headerText : "Submission Confirmation",
            bodyText : "By submitting, this entry will be tagged as “DONE” for judging. Until you have finalized your scoring, you can still come back to this entry and update the scores you initially provided.",
            actionFunc : this.tagEntryDoneAndRedirect
        }
        
        var selectedEntry = entries.filter(entry => entry.entryId === parseInt(entryId))
        
        return (
            <Grid className="header-padding">
                <div id="judging-panel-container">
                    <div id="judging-panel-header">
                        <span>Judging Panel</span>
                    </div>
                    <div className="panel-container-divider" />
                    <div id="entry-details-section" className="section-margin">
                        <div className="panel-header">
                            <span>Entry Details</span>
                        </div>
                        <div id="judging-details-section">
                            <div id="entry-img-holder">
                                <img id="entry-img" src="/assets/images/gundam.jpg" alt="400 x 300" />
                            </div>
                            <div>
                                <p>{selectedEntry[0].entryName}</p>
                                <span id="entry-desc">{selectedEntry[0].entryDescription}</span>
                            </div>
                        </div>
                    </div>
                    <div className="section-margin">
                        <div className="panel-header">
                            <span>Criteria for Judging</span>
                        </div>
                        {
                            criterias.map(criteriaItem => 
                                <CriteriaScoring 
                                    key={criteriaItem.criteriaId} 
                                    entryId={entryId} 
                                    criteria={criteriaItem} 
                                    criteriaScoresList={entryCriteriaScores} 
                                    scoreAddFunc={addCriteriaScore} 
                                    scoreUpdateFunc={updateCriteriaScore} 
                                    scoreAvgFunc={this.calculateAverageScore}
                                />    
                            )
                        }
                        <Row>
                            <Col md={11}>
                                Average Score
                            </Col>
                            <Col md={1}>
                                <span><strong>{ this.state.avgScore }</strong></span>
                            </Col>
                        </Row>
                    </div>
                    <div className="section-margin">
                        <div className="panel-header">
                            <span>Comments</span>
                        </div>
                        <EntryComments 
                            entryList={entries} 
                            entryIdValue={parseInt(entryId)}
                            entryCommentsList={entryComments} 
                            entryCommentAddFunc={addEntryComments} 
                            entryCommentUpdateFunc={updateEntryComments} 
                        />
                    </div>
                    <div className="panel-container-divider" />
                    <div id="judging-opts">
                        <button className="btn btn-danger">Reset</button>
                        <button className="btn btn-success" onClick={this.toggleDisplayConfirmationModal}>Submit</button>
                    </div>
                </div>
                {
                    (true === this.state.displaySubmitModal) ?
                        <ConfirmationModal 
                            closeModalFunc={this.toggleDisplayConfirmationModal} 
                            modalDetailsObject={confirmationModalObject} 
                            historyObj={history} 
                        />
                    :
                    ""
                }
            </Grid>
        )
    }
}

export default JudgingPanel;


