import React, { Component } from 'react'
import { Grid, Row, Col } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'

class ReviewScoring extends Component {
    constructor() {
        super() 
        
        this.state = {
            reviewScoringList : []
        }
        
        this.calculateAverageScore = this.calculateAverageScore.bind(this);
        this.determineRanking = this.determineRanking.bind(this);
        this.displayScoringPerCriteria = this.displayScoringPerCriteria.bind(this);
    }
    
    componentWillMount() {
        this.displayScoringPerCriteria(0);
    }
    
    displayScoringPerCriteria(criteriaId) {
        var computedList = [];
        const { entries, entryCriteriaScores } = this.props;
        
        var newList = entries.map(entry => {
//            entryCriteriaScores.map(entryScore => {console.warn(entryScore.entryId, entry.entryId, entryScore.criteriaId, parseInt(criteriaId), entryScore.entryId === entry.entryid, entryScore.criteriaId === criteriaId)});
            
            if (0 === parseInt(criteriaId)) {
                var filteredScoresPerEntry = entryCriteriaScores.filter(entryScore => entryScore.entryId === entry.entryId)
            } else {
                var filteredScoresPerEntry = entryCriteriaScores.filter(entryScore => entryScore.entryId === entry.entryId && entryScore.criteriaId === parseInt(criteriaId))
            }
            
            var entrySummary = {
                entryName : entry.entryName,
                avgScore : this.calculateAverageScore(filteredScoresPerEntry)
            }
            
            return entrySummary;
        })
        
        var sortedList = newList.slice().sort((a,b) => b.avgScore - a.avgScore) // SORT
        this.determineRanking( sortedList ); // RANKING
        
        this.setState({reviewScoringList : sortedList})
    }
    
    calculateAverageScore (scoresList) {
        var totalScore = 0;
        
        scoresList.map(scores => { totalScore = totalScore + scores.score })
        
        return (totalScore / scoresList.length);
    }
    
    determineRanking (entryList) {
        entryList[0].rank = 1;
        
        for (var x = 1; x < entryList.length; x++) {
            entryList[x].rank = entryList[x].avgScore === entryList[x-1].avgScore ? entryList[x-1].rank : x + 1;
        }
    }
    
    render () {
        var criteriaSelection = {}
        const { criterias } = this.props;
        
        return (
            <Grid className="header-padding max-height">
                <div id="review-scoring-container">
                    <div id="review-ranking-header-section">
                        <span>Review Scores</span>
                    </div>
                    <div className="scoring-container-divider" />
                    <div id="review-ranking-list-section">
                        <Row id="criteria-select">
                            <Col md={6}>
                                <select className="form-control" ref = {ref => criteriaSelection = ref} onChange={() => this.displayScoringPerCriteria(criteriaSelection.value)} >
                                    <option value={0}>Overall</option>
                                    {
                                        criterias.map(criteria => 
                                            <option value={criteria.criteriaId}>{criteria.criteriaName}</option>
                                        )
                                    }
                                </select>
                            </Col>
                        </Row>
                        <Row className="review-score-row">
                            <Col md={9}>
                                Entry Name
                            </Col>
                            <Col md={1}>
                                <span>Score</span>
                            </Col>
                            <Col md={2}>
                                <span>Position</span>
                            </Col>
                        </Row>
                        {
                            this.state.reviewScoringList.map(entry => 
                                <Row className="review-score-row">
                                    <Col md={9}>
                                        {entry.entryName}
                                    </Col>
                                    <Col md={1}>
                                        {entry.avgScore}
                                    </Col>
                                    <Col md={2}>
                                        {entry.rank}
                                    </Col>
                                </Row>
                            )
                        }
                    </div>
                    <div className="scoring-container-divider" />
                    <div id="review-ranking-opt-section">
                        <NavLink to="/judge-app/entry-list" className="btn btn-success">Back to List of Entries</NavLink>
                    </div>
                </div>
            </Grid>
        )
    }
}

export default ReviewScoring


